@font-face {
  font-family: 'TT Lakes';
  src: url(./fonts/TTLakes-Bold.eot);
  src: local("TT Lakes Bold"), local("TTLakes-Bold"), url(./fonts/TTLakes-Bold.eot?#iefix) format("embedded-opentype"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: bold;
  font-style: normal; }

/* nav block */
.menu {
  display: flex;
  flex-wrap: wrap;
  margin: 10px 30px;
  padding: 0;
  list-style: none;
  font-size: 16px; }

.menu .menu__item {
  display: flex;
  justify-content: flex-start;
  margin: 0 0 10px -4px; }

.menu .menu__link {
  display: block;
  white-space: nowrap;
  text-decoration: none;
  line-height: 24px;
  padding: 5px 20px;
  color: #7E7E82;
  background-color: transparent; }

.menu .menu__item--active {
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  background: #FFFFFF; }

.brands__content {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-start;
  flex-wrap: nowrap;
  padding: 14px;
  overflow-x: scroll; }

.brands__content .brands__item {
  display: block;
  box-sizing: border-box;
  height: 72px;
  min-width: 230px;
  padding: 5px 20px;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  background-image: url(./static/go_arrow.svg);
  background-position: 90% 50%;
  background-repeat: no-repeat;
  background-color: #ffffff;
  transition: border-left, border-right, 0.25s ease;
  margin: 10px; }

.brands__img {
  position: relative;
  top: 50%;
  transform: translateY(-50%); }

.brands__content .brands__item:hover {
  border-radius: 12px;
  border-left: 5px solid #FF3E79;
  border-right: 5px solid #FF3E79;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25); }

.tech__content {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-start;
  flex-wrap: nowrap;
  padding: 14px;
  overflow-x: scroll; }

.tech__content .tech__item {
  display: block;
  box-sizing: border-box;
  height: 155px;
  min-width: 230px;
  padding: 15px 20px;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  background-image: url(./static/go_arrow.svg);
  background-position: 90% 85%;
  background-repeat: no-repeat;
  background-color: #ffffff;
  color: #1B1C21;
  font-size: 16px;
  text-decoration: none;
  transition: border-left, border-right, 0.25s ease;
  margin: 10px; }

.tech__content .tech__item:hover {
  border-radius: 12px;
  border-left: 5px solid #FF3E79;
  border-right: 5px solid #FF3E79;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25); }

/* prices (start) */
.price-content {
  display: flex;
  margin: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
  min-height: 200px; }

.price-table {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding: 0;
  margin: 0;
  list-style: none;
  margin: 0 15px 20px 0; }

.price-table__row {
  position: relative;
  height: 160px;
  width: 220px;
  background-color: #ffffff;
  border-radius: 6px;
  font-size: 14px;
  padding: 15px;
  color: #7E7E82; }

.price-table__cell:nth-child(2n) {
  margin-bottom: 10px;
  color: #1B1C21; }

.price-table__cell:last-child {
  position: absolute;
  bottom: 15px;
  right: 15px; }

.price-table .price__order {
  background-color: #FF3E79;
  width: 120px;
  height: 32px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  color: #ffffff; }

/* prices (end) */
.site {
  font-family: "TT Lakes";
  background-color: #F2F2F2;
  color: #1B1C21;
  max-width: 320px;
  margin: 10px auto; }

.main {
  background-color: #f8f8f8; }

.title-section {
  border-top: 1px solid #D9FFF5;
  background: -webkit-gradient(linear, left top, right top, from(#F8F8F8), to(#FFFFFF));
  background: -o-linear-gradient(left, #F8F8F8 0%, #FFFFFF 100%);
  background: linear-gradient(90deg, #F8F8F8 0%, #FFFFFF 100%);
  text-transform: uppercase;
  padding: 20px 0 20px 32px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-weight: normal;
  font-size: 16px;
  color: #7E7E82; }

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
  background-color: #FF3E79;
  border-radius: 50%;
  background-position: 50% 50%;
  background-repeat: no-repeat; }

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: #ffffff;
  height: 85px; }

.header__divider {
  height: 32px;
  width: 2px;
  background-color: #EAEAEA; }

.button {
  font-family: "TT Lakes";
  font-size: 16px;
  border: none;
  cursor: pointer;
  background-color: transparent; }

.button__text {
  font-size: 16px;
  height: 40px;
  line-height: 40px; }

.icon--burger {
  background-image: url(./static/icon_burger.svg); }

.icon--repair {
  background-image: url(./static/icon_repair.svg); }

.icon--checkstatus {
  background-image: url(./static/icon_checkstatus.svg); }

.button--readmore {
  background-image: url(./static/icon_arrows_down.svg);
  background-position: 0% 50%;
  background-repeat: no-repeat;
  padding-left: 30px; }

.service {
  border-top: 1px solid #D9FFF5; }

.service__title {
  margin: 0;
  padding: 0 32px;
  height: 40px;
  font-size: 28px;
  font-weight: bold;
  line-height: 40px;
  letter-spacing: -0.6px;
  white-space: nowrap;
  border-left: 3px solid #41F6D7;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1; }

.service__header {
  margin: 24px 0; }

.service__buttons--visibility {
  display: none; }

.service__text--visibility {
  display: none; }

.content__text {
  margin: 30px; }

.content__img {
  max-width: 100%; }

.price__text {
  margin: 30px; }

.price__link {
  font-family: "TT Lakes";
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  text-decoration: none;
  color: #1B1C21; }

.footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-top: 1px solid #D9FFF5;
  color: #7E7E82;
  padding: 10px;
  font-size: 14px; }

.footer .footer__text {
  text-align: center;
  margin-bottom: 10px; }

.footer .footer__link {
  text-decoration: none;
  color: #7E7E82; }

.footer .footer__link:hover {
  text-decoration: underline; }
